<template>
  <div class="cont" ref="cont">
    <div>
      <swiper></swiper>
      <div class="musicList">
        <h3 class="p10">热门歌曲</h3>
        <dl v-for="item in musicList" class="p10">
          <dt>
            <img v-lazy="item.src" alt="">
          </dt>
          <dd>
            <h4 class="p10">{{item.name}}</h4>
            <p class="p10">{{item.singer}}</p>
          </dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
  import swiper from './include/swiper.vue'
  import axios from 'axios'
  import BScroll from 'better-scroll'
  export default {
    data() {
      return {
        musicList: null
      }
    },
    mounted() {
      this.getMusicList();
    },
    methods: {
      getMusicList() {
        axios.get("/musicList").then((result) => {
          this.musicList = result.data;
          setTimeout(() => {
            this.scroll = new BScroll(this.$refs.cont);
          }, 30);
        });
      },
    },
    components: {
      swiper
    }
  }

</script>

<style scoped>
  dl{
    display: flex;
  }
  dt{
    width: .8rem;
    height: .8rem;
  }
  dt img{
    width: 100%;
  }
</style>



