<template>
    
  <swiper :options="swiperOption" ref="mySwiper">
    <!-- slides -->
    <swiper-slide v-for="(item,index) in bannerList" :key="index"><img :src="item.src" alt=""></swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination" slot="pagination"></div>
    <div class="swiper-button-prev" slot="button-prev"></div>
    <div class="swiper-button-next" slot="button-next"></div>
  </swiper>
</template>
<script>
  import {
    swiper,
    swiperSlide
  } from 'vue-awesome-swiper';
  import axios from 'axios'
  const opt = {
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
        type: 'bullets'
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
      autoplay: {
        delay: 3000,
        disableOnInteraction: false
      },
      loop: true,
      slidesPerView: 1
    };
  export default {
    name: 'carrousel',
    data() {
      return {
        swiperOption: opt,
        bannerList: null
      }
    },
    components: {
      swiper,
      swiperSlide
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
        axios.get('/banner').then((result)=>{
            this.bannerList=result.data;
        })
    }
  }

</script>
<style>
    @import url('swiper/dist/css/swiper.css');
    .swiper-container{
        height: 2.25rem;
    }
    .swiper-slide{
        height: 2.25rem;
    }
    .swiper-slide img{
        width: 100%;
    }
</style>



