<template>
  <div>
    <nav id="title">
      <ul class="list">
        <li v-for="(item,index) in navList">
            <router-link :to="item.link">{{item.name}}</router-link>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        navList: [{
            name: '推荐音乐',
            link:'/index'
          },
          {
            name: '歌手排行',
            link: '/singer'
          },
          {
            name: '热歌榜',
            link: '/hot'
          },
          {
            name: '搜索',
            link: '/search'
          }
        ]
      }
    }
  }

</script>

<style scoped>
  .router-link-exact-active{
    height: 100%;
    width: .7rem;
    border-bottom: 2px solid red;
    display: inline-block;
  }

</style>



