import Index from '@/components/index'
import singer from '@/components/singer'
import hot from '@/components/hot'
import search from '@/components/search'

export default [
    {
        path: '/',
        name: '/',
        redirect: '/index'
    },
    {
        path: '/index',
        name: 'index',
        component: Index
    },
    {
        path: '/singer',
        name: 'singer',
        component: singer
    },
    {
        path: '/hot',
        name: 'hot',
        component: hot
    },
    {
        path: '/search',
        name: 'search',
        component: search
    }
]


